);\n  }\n  25% {\n    transform: scale(0.9, 1.1);\n  }\n  50% {\n    transform: scale(1.1, 0.9);\n  }\n  75% {\n    transform: scale(0.95, 1.05);\n  }\n}\n\n.flx-ctr {\n  justify-content: center;\n  display: flex;\n}\n\n.break {\n  flex-basis: 100%;\n  height: 0;\n}\n\n.wrap {\n  flex-wrap: wrap;\n}\n\n.ReactTags__tagInput {\n  position: relative;\n  display: inline-block;\n}\n\ninput {\n  color: vars.$darkgray;\n  font-size: inherit;\n  font-family: inherit;\n  background-color: vars.$white-full;\n  padding: 0.3em 0.45em;\n  border: 2px solid transparent;\n  transition: background-color 0.3s ease-in-out;\n  border-radius: 4px;\n}\n\ninput:focus {\n  outline: none;\n}\n\ninput::placeholder {\n  color: vars.$gray;\n}\n\nspan.line {\n  position: absolute;\n  background-color: vars.$green;\n  transition: transform 0.1s ease;\n}\n\nspan.bottom,\nspan.top {\n  height: 2px;\n  left: 0;\n  right: 0;\n  transform: scaleX(0);\n}\n\nspan.left,\nspan.right {\n  width: 2px;\n  top: 0;\n  bottom: 0;\n  transform: scaleY(0);\n}\n\nspan.bottom {\n  bottom: 0;\n  transform-origin: bottom right;\n}\n\ninput:focus ~ .bottom {\n  transform-origin: bottom left;\n  transform: scaleX(1);\n}\n\nspan.right {\n  right: 0;\n  transform-origin: top right;\n  transition-delay: 0.05s;\n}\n\ninput:focus ~ .right {\n  transform-origin: bottom right;\n  transform: scaleY(1);\n}\n\nspan.top {\n  top: 0;\n  transform-origin: top left;\n  transition-delay: 0.15s;\n}\n\ninput:focus ~ .top {\n  transform-origin: top right;\n  transform: scaleX(1);\n}\n\nspan.left {\n  left: 0;\n  transform-origin: bottom left;\n  transition-delay: 0.25s;\n}\n\ninput:focus ~ .left {\n  transform-origin: top left;\n  transform: scaleY(1);\n}\n\n.ReactTags__tagInput {\n  margin: 18px 5px 0;\n}\n\ndiv.ReactTags__selected span.ReactTags__tag {\n  background: vars.$white-full;\n  color: vars.$darkgreen;\n  font-size: 16px;\n  display: inline-block;\n  padding: 5px;\n  margin: 8px 6px 0;\n  cursor: move;\n  border-radius: 4px